<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">TPO Dispatch</h1>
      </div>
      <div class="card" v-for="(req, index) in reqs" :key="index">
        <v-layout @click="delete_request(req.id)" justify-center class="delete mb-2">
          <span class="group">
            <v-icon large class="white--text">delete</v-icon>
          </span>
        </v-layout>
        <div class="card-body">
          <h5 class="card-title">
            <span>Stock #:</span>
            {{req.stock_number}}
          </h5>
          <p class="card-text">
            <span>Sales Associate:</span>
            {{req.sales_associate}}
          </p>
          <p class="card-text">
            <span>Deal #:</span>
            {{req.deal_number}}
          </p>
          <p class="card-text">
            <span>Customer Name:</span>
            {{req.customer_name}}
          </p>
          <p class="card-text">
            <span>Year:</span>
            {{req.year}}
          </p>
          <p class="card-text">
            <span>Model:</span>
            {{req.model}}
          </p>
          <p class="card-text">
            <span>Detail Service:</span>
            {{req.detail_service}}
          </p>
          <p
            v-if="req.customer_care_package == 'Yes'"
            class="card-text badge yesccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-else-if="req.customer_care_package == 'No'"
            class="card-text badge noccp"
          >Customer Care Package: {{req.customer_care_package}}</p>
          <p
            v-if="req.accessories == 'Yes'"
            class="card-text badge yesccp"
          >Accessories: {{req.accessories}}</p>
          <p
            v-else-if="req.accessories == 'No'"
            class="card-text badge noccp"
          >Accessories: {{req.accessories}}</p>
          <p class="card-text" v-if="req.accessories == 'Yes'">
            <span>List Accessories:</span>
            {{req.list_accessories}}
          </p>
          <hr />
          <form
            @submit.prevent="updateSalesReq(req.status_sales, req.sales_manager, req.id, req.password)"
          >
            <label for="status_sales">Approval Status</label>
            <select
              id="status_sales"
              class="form-control form-control-lg"
              v-model="req.status_sales"
            >
              <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
            </select>
            <label for="sales_manager">Manager</label>
            <select
              id="sales_manager"
              class="form-control form-control-lg"
              v-model="req.sales_manager"
            >
              <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
            </select>
            <label for="password">PIN</label>
            <input class="form-control form-control-lg" type="password" v-model="req.password" />
            <v-btn color="success" type="submit">Submit</v-btn>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "New",
  data() {
    return {
      reqs: [],
      approvals: ["Approved", "Denied"],
      managers: [
        "Barry Laird",
        "Rolando Rocha",
        "Brandon Koke",
        "Yvone Garcia",
        "Don Wilkinson",
        "Joshua Barazza",
        "Bryan Barton"
      ]
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("type_of_vehicle", "==", "Pre-owned")
        .where("status_sales", "==", "In progress...")
    };
  },
  methods: {
    updateSalesReq(status, manager, id, password) {
      if (status !== "" && manager !== "") {
        if (
          (manager === "Barry Laird" && password === "2419") ||
          (manager === "Rolando Rocha" && password === "7206") ||
          (manager === "Don Wilkinson" && password === "5563") ||
          (manager === "Joshua Barazza" && password === "7030") ||
          (manager === "Brandon Koke" && password === "8790") ||
          (manager === "Yvone Garcia" && password === "4598") ||
          (manager === "Bryan Barton" && password === "7412")
        ) {
          if (status === "Approved") {
            db.collection("makeReady")
              .doc(id)
              .update({
                status_sales: "Approved",
                sales_manager: manager,
                status_finance: "In progress...",
                status_carwash: "In progress...",
                status_parts: "Complete",
                sales_completion_timestamp: new Date(),
                password: password
              });
          } else {
            db.collection("makeReady")
              .doc(id)
              .update({
                status_sales: "Denied",
                sales_manager: manager,
                sales_completion_timestamp: new Date(),
                password: password,
                status: "Complete"
              });
          }
        } else {
          alert("Either the manager is incorrect or the PIN does not match");
        }
      }
    },
    delete_request(id) {
      let person = prompt("Please enter your sales manager PIN");
      if (
        person != null &&
        person != "" &&
        // Brian Barton
        (person == "6969" ||
          // Yvone Garcia
          person == "4598" ||
          // Miguel Valdez
          person == "8790" ||
          // Joshua Barazza
          person == "7030" ||
          // Don Wilkinson
          person == "5563" ||
          // Rolando Rocha
          person == "7206" ||
          // Barry Laird
          person == "2419")
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            license_plate: "",
            parts_associate: "",
            status_parts: "",
            parts_completion_timestamp: "",
            parts_claimed_timestamp: "",
            send_to_parts: "",
            invoice_number: "",
            mats: "",
            mats_shelf: "",
            parts_state: "",
            finance_completion_timestamp: "",
            finance_claimed_timestamp: "",
            finance_associate: "",
            status_finance: "",
            send_to_finance: "",
            finance_state: "",
            carwash_claimed_timestamp: "",
            carwash_complete_timestamp: "",
            status_carwash: "",
            delivery_bay: "",
            checked_mats: "",
            carwash_associate: "",
            send_to_carwash: "",
            carwash_state: "",
            license_plate_installed: "",
            sales_completion_timestamp: "",
            status_sales: "'",
            sales_manager: "",
            password: "",
            delete_timestamp: new Date(),
            delete_status: true,
            delete_associate: person,
            status: "deleted"
          });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 20px;
}

.btn-primary {
  background-color: #0096a5;
  border-color: #0096a5;
}

.container-fluid {
  width: 90%;
}

.alertStyle {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 10%; /* Full height (cover the whole page) */
  padding-top: 40px;
  margin: auto 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  vertical-align: middle;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}

.card-title,
.card-title span {
  font-weight: bold;
  font-size: 1.6rem;
}

.card-title {
  font-weight: bold;
  font-size: 1.6rem;
}

span {
  font-weight: bold;
  font-size: 1.1rem;
}

label {
  font-weight: bold;
  font-size: 1.1rem;
}

form label {
  margin-top: 10px;
}

p {
  font-size: 1.2rem;
}

.delete {
  opacity: 0;
  cursor: pointer;
  background-color: #e46868;
}

.delete:hover {
  opacity: 100;
}
</style>



