<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <div class="container" id="nav-background">
        <router-link class="navbar-brand" to="/">Make Ready | BMW of SA</router-link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav ml-auto">
            <li v-for="(link, index) in links" :key="index" class="nav-item active">
              <router-link class="nav-link ml-sm-2" :to="link.path">{{link.title}}</router-link>
            </li>
            <li class="nav-item dropdown">
              <a
                class="nav-link dropdown-toggle"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >Dashboards</a>
              <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <router-link
                  v-for="(dashboard, i) in dashboards"
                  :key="i"
                  :to="dashboard.path"
                  class="dropdown-item"
                >{{ dashboard.title }}</router-link>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      drawer: {
        // sets the open status of the drawer
        open: false,
        // sets if the drawer is shown above (false) or below (true) the toolbar
        clipped: false,
        // sets if the drawer remains visible all the time (true) or not (false)
        permanent: false
      },
      links: [
        {
          title: "Request",
          path: "/"
        },
        {
          title: "New Car",
          path: "/newcar"
        },
        {
          title: "TPO",
          path: "/preowned"
        },
        {
          title: "Loan Car",
          path: "/loancar"
        },
        {
          title: "Parts",
          path: "/parts"
        },
        {
          title: "Finance",
          path: "/finance"
        },
        {
          title: "Detail",
          path: "/carwash"
        },
        {
          title: "Bugs",
          path: "/bugs"
        }
      ],
      dashboards: [
        {
          title: "In Progress",
          path: "/metrics"
        },
        {
          title: "Last 50 Complete",
          path: "/lastcomplete"
        },
        {
          title: "Last 50 Denied",
          path: "lastdenied"
        },
        {
          title: "Recently Deleted",
          path: "/deleted"
        }
      ]
    };
  },
  methods: {
    toggleDrawer() {
      if (this.drawer.permanent) {
        this.drawer.permanent = !this.drawer.permanent;
        // set the clipped state of the drawer and toolbar
        this.drawer.clipped = true;
        this.toolbar.clippedLeft = true;
      } else {
        // normal drawer
        this.drawer.open = !this.drawer.open;
      }
    }
  }
};
</script>

<style scoped>
.v-navigation-drawer > .v-list .v-list__tile__title {
  height: 60px;
}
#nav-background {
  padding: 5px;
}
</style>





